import * as React from "react";

const Image = (props) => {
    let srcSet = '';
    if (props && props.srcSet) {
        const splitSrcSet = props.srcSet.split(" ");
        const out = [];
        for (let i = 0; i < splitSrcSet.length; i+= 2) {
            out.push(`${splitSrcSet[i]} ${splitSrcSet[i + 1]}`)
        }
        srcSet = out.join(', ');
        
    }

    // const sizes =  `calc(80vw - 32px)`;
    return (<img {...props} srcSet={srcSet}  />);
};

export default Image;